
body {
    padding: 0;
    margin: 0;
  }
  
  div .cover {
    height: 850px;
    width: 100%;
    background: #000 none repeat scroll 0% 0%;
    background-image: url('../DailyTwo/productiveday.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
  }
  
  div .black-shadow-cover {
    height: 850px;
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.7);
  }
  


.todo-app{


margin:4% 10%;
    height:700px;
    width:80%;
    border:1px solid white;
    box-shadow: 1px 1px 5px 5px lightblue;
}


.todo-app .todo-header{
font-family: sans-serif;
text-align: center;
color:white;
margin-top:40px;
font-family: "Great Vibes", cursive;

}


.todo-app .todo-form{
 text-align: center;
    height:20%;
    width:100%;
    
   
}

.todo-app .todo-form .data-input{
margin-top:7%;
   height:50px;
   width:65%;
   margin-top:10px;
   padding:0;
   font-size: 18px;
border-color: rgb(39, 204, 204);
background: transparent;
color:white;
 padding-left:5%; 
 border-radius:5px;
}

.todo-app .todo-form .data-input:focus{
   box-shadow: 1px 1px 2px 2px lightblue;
}


.todo-app .todo-form .add-btn{

    height:50px;
    background-color: rgb(31, 92, 92);

border: none;
border-radius: 5px;
margin-left: 3%;    
color: white;
font-weight: bold;
 }


 .todo-app .todo-form button:hover{
   box-shadow: 1px 1px 2px 2px lightblue;
 }

 .fulldata .single-todo{
 height:10%;
 width:78%;
 border:1px solid white;
margin: 4% 10%;
 color:white;
 display: grid;
 grid-template-columns: auto auto;
 border-radius: 10px;

 }



 .fulldata .single-todo .todo-text p{

  margin-right:20%;
    font-size: 18px;
  
 }



 .fulldata .single-todo .todo-delete button{

margin-top: 25%;
    margin-left:60%;
 
    
 }

 .fulldata .single-todo .todo-delete button:hover{
   background-color: rgb(166, 167, 167);
  
 }




 .fulldata .single-todo .delete{
  
background: transparent;
color: white;
border-radius: 5px;
border:none
 }

 .fulldata .single-todo .delete:hover{
   color:rgb(197, 16, 16);
}
